@import url(https://fonts.googleapis.com/css?family=Roboto);
html {
  font-size: 62.5%;
}

body {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  background-color: #333745;
  font-family: roboto;
}

button {
  cursor: pointer;
}

button:disabled {
  cursor: default;
}

.header {
  background: #001400;
  color: white;
  margin: 0;
  padding: 1.6rem 0;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.header__title {
  font-size: 3.2rem;
  margin: 0;
}

.header__subtitle {
  color: #979aaa;
  font-size: 1.6rem;
  font-weight: 500;
  margin: 0;
}

@media (min-width: 45rem) {
  .header {
    margin-bottom: 4.8rem;
  }
}
.container {
  display: flex;
  justify-content: center;
  align-items: initial;
}

.poll {
  background: #464b5e;
  margin-bottom: 4.8rem;
  margin-right: 2.4rem;
  margin-left: 2.4rem;
  width: 700px;
  max-width: 700px;
  position: fixed;
  top: 100px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.poll-header {
  background: #3c4251;
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 1.6rem;
}

.poll-header__title {
  margin: 0;
}

.poll-header__flag {
  margin: 0;
  -webkit-transform: scale(3) translateY(-0.4rem);
          transform: scale(3) translateY(-0.4rem);
  -webkit-transform-origin: right top;
          transform-origin: right top;
}

.poll-content {
  color: white;
  margin: 0;
  padding: 1.6rem;
  text-align: center;
  border-bottom: 1px solid #5c627b;
  font-weight: 500;
  font-size: 2rem;
  word-break: break-all;
}

input[type=radio] {
  position: absolute;
  visibility: hidden;
  display: none;
}

label {
  color: #9a929e;
  display: inline-block;
  cursor: pointer;
  font-weight: bold;
  padding: 5px 20px;
}

input[type=radio]:checked + label {
  color: #ccc8ce;
  background: #675f6b;
}

label + input[type=radio] + label {
  border-left: solid 3px #675f6b;
}

.radio-group {
  border: solid 3px #675f6b;
  display: inline-block;
  margin: 20px;
  border-radius: 10px;
  overflow: hidden;
}

.poll-button {
  border: none;
  background: #404040;
  color: #ffffff !important;
  font-weight: 100;
  padding: 20px;
  text-transform: uppercase;
  border-radius: 6px;
  display: inline-block;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  margin-bottom: 1.6rem;
}

.poll-button:hover {
  color: #404040 !important;
  font-weight: 700 !important;
  letter-spacing: 3px;
  background: white;
  -webkit-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  -moz-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #001400;
  color: white;
  text-align: center;
}
